<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <AnimateHeader
      bg-color="#FFA70F"
      menu-bg-color="#FB940C"
      color="#FFFFFF"
      title="图文教程"
      :show-back-button="true"
      :on-back-button-click="back"
      :animate="false"
    >
    </AnimateHeader>
    <scroller class="page-content">
      <div class="step step-1"></div>
      <div class="step step-2"></div>
      <div class="step step-3"></div>
      <div class="step step-4"></div>
      <div class="step step-5"></div>
    </scroller>
  </div>
</template>

<style lang="scss" scoped>
  @import "../../scss/mixins";

  .page-content {
    padding-top: 50px;

    .step {
      height: 631px;

      $img-size: 375px 631px;

      &.step-1 {
        @include background-image(url(//qianka.b0.upaiyun.com/images/b279af8159a9c81f77ba479e3dc5c2c2.png), $img-size);
      }

      &.step-5 {
        height: 48px;
      }
    }
  }
</style>

<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'

  const backgroundImg = [
    '//qianka.b0.upaiyun.com/images/fa513e4e4752a2591fbdd8eb00386fdb.png',
    '//qianka.b0.upaiyun.com/images/35dad1d125b23b278176530c06be5283.png',
    '//qianka.b0.upaiyun.com/images/7da4887ad14cf5be72edfae92bda4617.png'
  ]
  export default {
    components: {
      AnimateHeader
    },

    mounted () {
      let img = document.querySelectorAll('.step')
      let index = 1
      const loadImg = setInterval(() => {
        img[index].style.backgroundImage = `url(${backgroundImg[index - 1]})`
        img[index].style.backgroundSize = '375px 631px'
        index === 3 ? clearInterval(loadImg) : index += 1
      }, 300)
    },

    methods: {
      back () {
        let backRoute = this.$route.name.split('_')[0]
        $router.back('/' + backRoute)
      }
    }
  }
</script>



